.about-dev {
  height: 100vh;
  background-color: #033C51;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.how-arrive {
  width: 60%;
  text-align: center;
}

.how-arrive h2 {
  font-size: 45px;
  font-family: 'Roboto Mono', monospace;
  text-shadow: 2px 2px 6px black;
  color: yellow;
  margin-bottom: 35px;
}

.how-arrive p {
  font-size: 25px;
  font-family: 'Roboto Mono', monospace;
  text-shadow: 2px 2px 6px black;
  color: white;
}

.how-arrive a {
  color: #2FC18C;
  font-weight: bold;
}