.projects {
  height: 100vh;
  background-color: darkorange;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.projects h2 {
  font-size: 45px;
  font-family: 'Roboto Mono', monospace;
  text-shadow: 2px 2px 6px black;
  color: yellow;
}

.projects ul {
  list-style: none;
  display: flex;
}

.projects ul a {
  text-decoration: none;
}

.projects ul li {
  border: solid 3px white;
  border-radius: 20px;
  width: 260px;
  background-color: #033C51;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 200px;
  margin: 20px;
  box-shadow: 0 0 25px black;
  transition: 200ms;
  justify-content: space-between;
}

.projects ul li:hover {
  border: solid 3px yellow;
  cursor: pointer;
}

.projects ul li h3 {
  font-size: 20px;
  padding: 10px;
  border-radius: 0 0 18px 18px;
  background-color: #023041;
  font-family: 'Roboto Mono', monospace;
  width: 100%;
  text-align: center;
  color: white;
}

.img-project {
  margin-top: 15px;
}

.in-progress {
  margin-top: 50px;
  font-size: 25px;
  font-family: 'Roboto Mono', monospace;
  color: white;
  font-weight: bold;
}

.more-projects {
  font-family: 'Roboto Mono', monospace;
  font-size: 30px;
  font-weight: bold;
  display: flex;
  align-items: center;
  color: white;
  text-shadow: 2px 2px 6px black;
}

.more-projects img {
  margin-left: 40px;
}


