.soft-skills {
  height: 100vh;
  background-color: #033C51;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.container-img-title {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 50%;
}

.container-list-title {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.container-list-title h2 {
  margin-top: 40px;
  font-size: 45px;
  font-family: 'Roboto Mono', monospace;
  text-shadow: 2px 2px 6px black;
  color: yellow;
}

.soft-list {
  margin-top: 40px;
  display: flex;
  width: 50%;
  flex-direction: column;
  list-style: none;
}

.soft-list li {
  display: flex;
  width: 80%;
  flex-direction: column;
  margin-top: 30px;
}

.soft-list li h3 {
  font-size: 25px;
  font-family: 'Roboto Mono', monospace;
  text-shadow: 2px 2px 6px black;
  color: yellow;
}

.soft-list li p {
  font-size: 20px;
  color: white;
  margin-top: 10px;
  font-family: 'Roboto Mono', monospace;
}
